import * as React from "react";
import Paper from '@material-ui/core/Paper';
import CardActionArea from '@material-ui/core/CardActionArea';
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Dialog from "@material-ui/core/Dialog";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/HighlightOffSharp";
import Slide from "@material-ui/core/Slide";
import Grid from "@material-ui/core/Grid";
import intl from 'react-intl-universal';

const useStyles = makeStyles((theme) => ({

    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    square: {
        color: theme.palette.getContrastText("#26c6da"),
        backgroundColor: "#26c6da",
        width: 'auto',
        margin: theme.spacing(3)
    },
    title: {
        flex: 1,
    }
}));

const InteractiveItem = (props: any) => {

    const classes = useStyles(props.item);
    const [open, setOpen] = React.useState(true);

    const theme = useTheme();

    const handleClose = () => {
        setOpen(false);
        props.resetContentFlag();
    };

    const Transition = React.forwardRef(function Transition(props, ref) {
        return <Slide direction="up" ref={ref} {...props} />;
    });
    return (
        <div>
            <Dialog
                fullScreen
                scroll="paper"
                open={open}
                onClose={handleClose}
                TransitionComponent={Transition}
            >
                <AppBar>
                    <Toolbar>
                        <Typography variant="h5" className={classes.title}>
                            {intl.get('MARATHI_MULAKSHARE')}
                        </Typography>
                        <IconButton
                            edge="start"
                            color="inherit"
                            onClick={handleClose}
                            aria-label="close"
                        >
                            <CloseIcon fontSize="large" />
                        </IconButton>
                    </Toolbar>
                </AppBar>
                {props.contentData ? (
                    <div>
                        <Toolbar />
                        <Grid
                            container
                            spacing={3}
                            style={{ padding: 15 }}
                            direction="row"
                            justify="center"
                            alignItems="center"
                        >
                            {props.contentData.map((currentItem) => (
                                <Grid item xs={6} lg={6} xl={2} key={currentItem.id}>
                                    <CardActionArea >
                                        <Paper className={classes.paper} elevation={1}>
                                            <Typography variant="h1" color="inherit">
                                                {currentItem.text}
                                            </Typography>
                                        </Paper>
                                    </CardActionArea>
                                </Grid>
                            ))}</Grid>
                    </div>
                ) : "Coming soon"}
            </Dialog>
        </div>
    );
};

export default InteractiveItem;