import * as React from "react";
import CardActionArea from '@material-ui/core/CardActionArea';
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardMedia from "@material-ui/core/CardMedia";
import InteractiveContent from './InteractiveContent';
import { mulakshar } from './Constants';

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
  },
  media: {
    height: 160,
  },
}));

const InteractiveItem = (props: any) => {

  const classes = useStyles(props.item);
  const [open, setOpen] = React.useState(false);
  const [contentData, setContentData] = React.useState({});
  const theme = useTheme();

  const handleClickOpen = () => {
    setContentData(mulakshar);
    setOpen(true);

  };

  let resetContentFlag = () => {
    setOpen(false);
  }

  return (
    <div>
      {props.item ? (
        <Card className={classes.root}>
          <CardActionArea onClick={handleClickOpen}>
            <CardMedia
              className={classes.media}
              image={props.item.url}
            />
          </CardActionArea>
        </Card>
      ) : null}

      {open && <InteractiveContent contentData={contentData} resetContentFlag={resetContentFlag} />}
    </div>
  );
};

export default InteractiveItem;