import * as React from "react";
import Grid from "@material-ui/core/Grid";
import { marathiInteractiveChoices } from "./Constants";
import InteractiveItem from "./InteractiveItem";

class InteractiveDashboard extends React.Component {
    state = {
    };

     handleDashboardView = (dashboardFlag: boolean, contentData, navTitle: string) => {
         this.props.updateDashboardFlag(dashboardFlag, contentData, navTitle);
    }

    render() {
        return (
            <div>
                {marathiInteractiveChoices ? (
                    <Grid
                        container
                        spacing={3}
                        style={{ padding: 15}}
                        direction="row"
                        justify="center"
                        alignItems="center"
                    >
                        {marathiInteractiveChoices.map((currentItem) => (
                            <Grid item xs={6} lg={6} xl={2} key={ currentItem.id }>
                                <InteractiveItem item={currentItem} handleDashboardView={this.handleDashboardView} />
                            </Grid>
                        ))}
                    </Grid>
                ) : (
                        "No Items found"
                    )}
            </div>
        );
    }
}

export default InteractiveDashboard;
